import * as React from 'react';
import {ChangeEvent, SyntheticEvent, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {filterBoardOverview} from "../actions/commands";
import {makeGetBoardOverview} from "../selectors/boardList";

interface OwnProps {}

type BoardOverviewSearchProps = OwnProps;

const BoardOverviewSearch = (props: BoardOverviewSearchProps) => {
    const dispatch = useDispatch();

    const boardOverviewSelector = makeGetBoardOverview();
    const boardOverviewState = useSelector(boardOverviewSelector);

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
        dispatch(filterBoardOverview(e.target.value));
    };

    const handleClear = (e: SyntheticEvent) => {
        e.preventDefault();
        dispatch(filterBoardOverview(''));
    };

    const filter = boardOverviewState.filter;

    return <div className="item">
        <div className="ui icon input">
            <input type="text" placeholder="Search..." value={filter} onChange={handleChange} />
            {filter === '' && <i className="search icon" />}
            {filter !== '' && <i className="close link icon" style={{cursor: "pointer"}} onClick={handleClear} />}
        </div>
    </div>
};

export default BoardOverviewSearch;