import {List, Map} from "immutable";
import { Reducer } from 'redux';
import {DEQUEUE_BOARD_CHANGED, ENQUEUE_BOARD_CHANGED} from "../actions/constants";
import {BoardSync} from "../model";
import {InspectioBoardsAction} from "./index";


export interface BoardQueueState extends Map<string, BoardSync.BoardQueue>{

}

export const initialState: BoardQueueState = Map<string, List<BoardSync.BoardChanged>>();

const enqueue = (state: BoardQueueState, boardChanged: BoardSync.BoardChanged): BoardQueueState => {
    const boardQueue: BoardSync.BoardQueue = state.get(boardChanged.board, List());
    return state.set(boardChanged.board, boardQueue.push(boardChanged));
}

const dequeue = (state: BoardQueueState, events: BoardSync.BoardQueue): BoardQueueState => {
    const firstBoardChanged = events.first(new BoardSync.BoardChanged(BoardSync.defaultBoardChangedProps));

    let boardQueue: BoardSync.BoardQueue = state.get(firstBoardChanged.board, List());
    events.forEach(boardChanged => {
        boardQueue = boardQueue.filter(queuedBoardChanged => queuedBoardChanged.uid !== boardChanged.uid);
        state = state.set(boardChanged.board, boardQueue);
    })

    return state;
}

const reducer: Reducer<BoardQueueState, InspectioBoardsAction> = (state: BoardQueueState = initialState, action: InspectioBoardsAction): BoardQueueState => {
    switch (action.type) {
        case ENQUEUE_BOARD_CHANGED:
            return enqueue(state, action.payload.boardChanged);
        case DEQUEUE_BOARD_CHANGED:
            return dequeue(state, action.payload.events);
        default:
            return state;
    }

}

export {reducer as manageBoardQueues};