import {Map} from "immutable";
import React from 'react'
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Link} from "react-router-dom";
import {InjectedFormProps} from "redux-form";
import { Field, reduxForm } from 'redux-form/immutable';
import {Button} from "semantic-ui-react";
import {inspectio} from "../../routes";
import {EDIT_BOARD_FORM} from "./constants";

interface FormData  {
    uid: string,
    name: string;
}

export interface ImFormData extends Map<string, string> {
    toJS(): FormData
}

export interface OwnProps {

}

export interface PropsFromDispatch {

}


export type EditBoardFormProps = OwnProps & PropsFromDispatch & WithNamespaces & InjectedFormProps<ImFormData, OwnProps & WithNamespaces>;

const EditBoardForm = (props: EditBoardFormProps) => <form className="ui form" onSubmit={props.handleSubmit}>
    <div className="fields" style={{margin:"0"}}>
        <div className="inline field">
        <Field name="name" component="input" type="text" placeholder={props.t('insp.board.form.board_name')} />
        </div>
        <Button primary={true} type="submit" className="bottom aligned">{props.t('app.form.rename')}</Button>
        <Link to={inspectio} className="ui basic button noborder">{props.t('app.form.cancel')}</Link>
    </div>
</form>;

export default withNamespaces()(reduxForm<ImFormData, OwnProps & WithNamespaces>({form: EDIT_BOARD_FORM})(EditBoardForm));

