import {map} from "lodash";
import {call, delay, fork, put, select, take} from 'redux-saga/effects';
import { ActionType } from 'typesafe-actions';
import {ResponseType} from "../../api/util";
import * as Socket from '../../Socket';
import * as Action from '../actions';
import { BoardSync } from '../model';
import {BoardVersion} from "../model/BoardVersion";
import { BoardSelector } from '../selectors';
import {marshalBoardChanged} from "./loadBoardHistoryFlow";


type SyncFlowAction = ActionType<typeof Action.Event.localBoardChanged>
    | ActionType<typeof Action.Command.syncBoard>
    | ActionType<typeof Action.Command.stopBoardSynchronisation>
    | ActionType<typeof Action.Event.remoteBoardChanged>
    | ActionType<typeof Socket.Action.Action.socketConnected>
    | ActionType<typeof Socket.Action.Action.socketConnectionLost>;

let socketConnectionLost = false;
let userId: string | null = null;
let username: string | null = null;
let currentBoard: string | null = null;
let localChangeSetBuffer: Array<ActionType<typeof Action.Event.localBoardChanged>> = [];
let boardVersionOnConnectionLost: null|BoardVersion = null;

const handleSyncFlowAction = function* (action: SyncFlowAction) {
    const socketStatus: Socket.Model.StatusModel.Status = yield select(Socket.Selector.StatusSelector.makeGetStatus());

    switch (action.type) {
        case Action.Type.LOCAL_BOARD_CHANGED:
            if(socketStatus.isConnected()) {
                yield put(Socket.Action.Action.sendSocketCmd(
                    action.payload.boardChanged.name,
                    action.payload.boardChanged.toJS(),
                    Action.Type.LOCAL_CHANGE_SET_AS_HEAD_VERSION,
                ));
            }
            if(socketConnectionLost) {
                localChangeSetBuffer.push(action);
            }
            break;
        case Action.Type.SYNC_BOARD:
            userId = action.payload.userId;
            username = action.payload.username;
            currentBoard = action.payload.boardId;

            yield put(Socket.Action.Action.connectSocket());
            yield take([Socket.Action.Type.SOCKET_CONNECTED]);
            yield put(Socket.Action.Action.listenOnSocketChannel('BoardChanged', Action.Type.REMOTE_BOARD_CHANGED));
            yield put(Socket.Action.Action.sendSocketCmd(
                'BoardOpened',
                {boardId: action.payload.boardId, userId: action.payload.userId, username: action.payload.username},
                Action.Type.REMOTE_BOARD_OPENED,
            ));
            break;
        case Action.Type.STOP_BOARD_SYNCHRONISATION:
            userId = null;
            username = null;
            currentBoard = null;
            socketConnectionLost = false;
            localChangeSetBuffer = [];
            boardVersionOnConnectionLost = null;

            yield put(Socket.Action.Action.stopListeningOnSocketChannel('BoardChanged'));
            break;
        case Action.Type.REMOTE_BOARD_CHANGED:
            const board = yield select(BoardSelector.makeBoardByIdSelector(action.payload.event.board));
            const remoteBoardChanged = BoardSync.remoteBoardChanged(action.payload.event);

            if (board && remoteBoardChanged.toBoardVersion().isNewerThan(board.version)) {
                yield put(Action.Command.enqueueBoardChanged(remoteBoardChanged));
            }
            break;
        case Socket.Action.Type.SOCKET_CONNECTION_LOST:
            socketConnectionLost = true;
            if(currentBoard) {
                const conLostBoard = yield select(BoardSelector.makeBoardByIdSelector(currentBoard));
                boardVersionOnConnectionLost = conLostBoard.version;
            }
            break;
        case Socket.Action.Type.SOCKET_CONNECTED:
            if(socketConnectionLost && currentBoard) {
                socketConnectionLost = false;

                yield put(Socket.Action.Action.listenOnSocketChannel('BoardChanged', Action.Type.REMOTE_BOARD_CHANGED));
                yield put(Socket.Action.Action.sendSocketCmd(
                    'BoardOpened',
                    {boardId: currentBoard, userId, username},
                    Action.Type.REMOTE_BOARD_OPENED,
                ));

                for(const localBoardChangedAction of localChangeSetBuffer) {
                    yield put(Socket.Action.Action.sendSocketCmd(
                        localBoardChangedAction.payload.boardChanged.name,
                        localBoardChangedAction.payload.boardChanged.toJS(),
                        Action.Type.LOCAL_CHANGE_SET_AS_HEAD_VERSION,
                    ));
                    yield delay(50);
                }
                localChangeSetBuffer = [];

                if(boardVersionOnConnectionLost) {
                    // Check if we missed some patches done remotely
                    const {response, error}: ResponseType = yield call(Action.Api.fetchNewerPatches, currentBoard, boardVersionOnConnectionLost);

                    if(response) {
                        const patches = map(response.data as any, (data: any) => marshalBoardChanged(data));

                        for(const patch of patches) {
                            yield put(Action.Command.enqueueBoardChanged(patch));
                        }
                    }
                }
                boardVersionOnConnectionLost = null;
            }
            break;
        default:
            // ignore
            break;
    }
};


export function* syncFlow() {


    while (true) {
        const action: SyncFlowAction = yield take([
            Action.Type.LOCAL_BOARD_CHANGED,
            Action.Type.SYNC_BOARD,
            Action.Type.STOP_BOARD_SYNCHRONISATION,
            Action.Type.REMOTE_BOARD_CHANGED,
            Socket.Action.Type.SOCKET_CONNECTION_LOST,
            Socket.Action.Type.SOCKET_CONNECTED,
        ]);

        yield fork(handleSyncFlowAction, action);
    }
}
