import {Map} from "immutable";
import React from 'react'
import {withNamespaces, WithNamespaces} from "react-i18next";
import {InjectedFormProps} from "redux-form";
import { Field, reduxForm } from 'redux-form/immutable'
import {Button} from "semantic-ui-react";
import {ADD_BOARD_FORM} from "./constants";

interface FormData  {
    uid: string,
    name: string;
}

export interface ImFormData extends Map<string, string> {
    toJS(): FormData
}

export interface OwnProps {

}

export interface PropsFromDispatch {

}


export type AddBoardFormProps = OwnProps & PropsFromDispatch & WithNamespaces & InjectedFormProps<ImFormData, OwnProps & WithNamespaces>;

const AddBoardForm = (props: AddBoardFormProps) => <form className="ui form" onSubmit={props.handleSubmit}>
    <div className="field">
        <Field name="name" component="input" type="text" placeholder={props.t('insp.board.form.board_name')} autoFocus={true} />
    </div>
    <Button primary={true} type="submit">{ props.t('insp.board.form.btn_add_save') }</Button>
</form>;

export default withNamespaces()(reduxForm<ImFormData, OwnProps & WithNamespaces>({form: ADD_BOARD_FORM})(AddBoardForm));