import {is, List} from "immutable";
import * as React from 'react';
import {WithNamespaces} from "react-i18next";
import {useDispatch, useSelector} from "react-redux";
import {RouteComponentProps} from "react-router";
import {
    Card,
    Container,
    Divider,
    Grid,
} from 'semantic-ui-react';
import {makeOrganizationSelector} from "../../Organization/selectors/selectOrganization";
import {makeGetCurrentUser} from "../../User/selectors/currentUser";
import * as Action from '../actions';
import {BoardModel} from "../model";
import AddBoardCard from "./AddBoardCard";
import {default as BoardCard} from "./BoardCard";
import {LoadingBoardCard} from "./LoadingBoardCard";
import {makeUserSettingSelector, makeUserSettingsLoadedSelector} from "../../User/selectors/userSettings";
import NewDomainModal from "./NewDomainModal";
import {useEffect, useState} from "react";
import {upsertUserSetting} from "../../User/actions/commands";

interface PropsFromState {
    boards: List<BoardModel.Board>,
    userQuota: number,
    loading: boolean,
}

interface PropsFromDispatch {
    setBoardToSnapshot: typeof Action.Command.setBoardToSnapshot;
    enqueueBoardChanged: typeof Action.Command.enqueueBoardChanged;
}

interface OwnProps {

}

export type BoardOverviewProps = PropsFromState & PropsFromDispatch & OwnProps;

export type EnhancedProps = BoardOverviewProps & WithNamespaces & RouteComponentProps;

export const BoardOverview = (props: EnhancedProps) => {
    const dispatch = useDispatch();
    const currentUser = useSelector(makeGetCurrentUser());
    const organization = useSelector(makeOrganizationSelector(currentUser.activeOrganization));
    const settingsLoaded = useSelector(makeUserSettingsLoadedSelector());
    const showHint = useSelector(makeUserSettingSelector('show_pb_domain_hint', false));
    const [popupOpen, setPopupOpen] = useState(false);

    useEffect(() => {
        if(settingsLoaded && showHint) {
            setPopupOpen(true);
        }
    }, [settingsLoaded, showHint])

    const exampleBoards = props.boards.filter(board => board.example);
    //
    const isAdmin = organization ? organization.isAdmin(currentUser.uid) : false;

    const handleCloseNewDomainModal = () => {
        setPopupOpen(false);
        dispatch(upsertUserSetting('show_pb_domain_hint', false));
    }

    return <Container>
    <Grid stackable={true} padded={true}>
    <Grid.Row>
        <Grid.Column> 
                <Card.Group itemsPerRow={4}>
                    <AddBoardCard disabled={props.userQuota === 0} />
                    {props.loading && <LoadingBoardCard/>}
                    {props.boards.filter(board => !board.example).sortBy(board => board.lastModified, (a, b) => a === b ? 0 : (a! > b! ? -1 : 1)  )
                        .map(board =>
                        <BoardCard key={board.uid} board={board}
                                   isAdmin={board.organization? isAdmin  || board.isCreator(currentUser.uid) : board.owner === currentUser.uid}
                                   setBoardToSnapshot={props.setBoardToSnapshot} enqueueBoardChanged={props.enqueueBoardChanged} />
                    ).toArray()}
                </Card.Group>
        </Grid.Column>
    </Grid.Row>
    {exampleBoards.count() > 0 && <Grid.Row>
        <Grid.Column>
            <Divider horizontal={true} content="Example Boards" />
        </Grid.Column>
    </Grid.Row>}
    {exampleBoards.count() > 0 && <Grid.Row>
        <Grid.Column>
            <Card.Group itemsPerRow={4}>
                {exampleBoards.sortBy(board => board.lastModified, (a, b) => a === b ? 0 : (a! > b! ? -1 : 1)  )
                    .map(board =>
                        <BoardCard key={board.uid} board={board} isAdmin={false} setBoardToSnapshot={props.setBoardToSnapshot} enqueueBoardChanged={props.enqueueBoardChanged} />
                    ).toArray()}
            </Card.Group>
        </Grid.Column>
    </Grid.Row>}
    </Grid>
        <NewDomainModal open={popupOpen} onClose={handleCloseNewDomainModal} />
    </Container>
};
