import {withNamespaces} from "react-i18next";
import { connect } from 'react-redux';
import {withRouter} from "react-router";
import { compose, lifecycle } from 'recompose';
import {bindActionCreators, Dispatch} from "redux";
import {Command} from "../actions";
import {AddBoard, AddBoardProps} from "../components/AddBoard";
import {BoardSelector} from "../selectors";

const mapStateToProps = BoardSelector.makeMapStateToPropsAddBoard();

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
    {
        addBoard: Command.addBoard,
    },
    dispatch
);

export default withNamespaces()(withRouter<AddBoardProps>(compose<AddBoardProps, AddBoardProps>(
    connect(mapStateToProps, mapDispatchToProps),
    lifecycle({

    })
)(AddBoard)));