import {fromJS} from "immutable";
import * as React from 'react';
import {useState} from "react";
import {WithNamespaces} from "react-i18next";
import {RouteComponentProps} from "react-router";
import {Message, Modal} from 'semantic-ui-react';
import {UserModel} from "../../User";
import {Command} from "../actions";
import {BoardModel} from "../model";
import {default as AddBoardForm, ImFormData} from './AddBoardForm';
import {useSelector} from "react-redux";
import {makeActiveUserOrganizationInfoSelector} from "../../Organization/selectors/selectUserOrganizationInfo";

interface PropsFromState {
    user: UserModel.UserInfo;
}

interface PropsFromDispatch {
    addBoard: typeof Command.addBoard;
}

interface OwnProps {
    open: boolean;
    onClose: () => void;
    disabled: boolean;
}

export type AddBoardProps = PropsFromState & PropsFromDispatch & OwnProps & WithNamespaces & RouteComponentProps;

const submitForm = (data: ImFormData, props: AddBoardProps): void => {
    const board = BoardModel.createUserBoard(props.user.uid, data.get('name') as BoardModel.BoardName);
    props.addBoard(board, props.history);
}

export const AddBoard = ( props: AddBoardProps) => {
    const [isAddingBoard, setIsAddingBoard] = useState(false);
    const activeOrganization = useSelector(makeActiveUserOrganizationInfoSelector());

    return <Modal open={ props.open} closeIcon={true} onClose={ props.onClose}>
    <Modal.Header>{  props.t("insp.board.add") }</Modal.Header>
    <Modal.Content>
        { props.disabled && !isAddingBoard && <Message icon="dollar" warning={true}
                                                       content={activeOrganization && activeOrganization.userIsGuest
                                                           ? props.t('insp.user.quota_reached_guest')
                                                           : activeOrganization && activeOrganization.explicitOrganization
                                                               ? props.t('insp.user.quota_reached')
                                                               : props.t('insp.user.quota_reached_user')
                                                       } />}
        {! props.disabled && <AddBoardForm initialValues={fromJS(BoardModel.defaultBoardProps)} onSubmit={(data) => {setIsAddingBoard(true); submitForm(data,  props)}} />}
        {isAddingBoard && <span>...</span>}
    </Modal.Content>
</Modal>};


