import * as _ from "lodash";

export const hasKey = <O>(key: string): (obj: O) => boolean => {
    return (obj: O): boolean => {
        if(!_.isObject(obj)) {
            return false;
        }

        return _.has(obj, key);
    }
};

export const isArray = <O>(): (obj: O) => boolean => {
    return (obj: O) => {
        return _.isArray(obj);
    }
};
